import React, { useState } from "react";

const RangeSlider = ({ value, min, max, step, handleChange }) => {
  return (
    <div>
      <input
        type="range"
        min={min}
        max={max}
        step={step}
        defaultValue={value}
        onChange={handleChange}
      />
      <span>{value} KM</span>
    </div>
  );
};

export default RangeSlider;
